/* Title */

@mixin title-default {
  color: var(--text-tale-title);
  font-family: var(--font-family);
  letter-spacing: -0.01em;
  font-weight: 400;
  line-height: 120%;
}

// Title on Desktop

@mixin h1-desk {
  @include title-default;
  font-size: px-to-rem(84);
}

@mixin h2-desk {
  @include title-default;
  font-size: px-to-rem(54);
}

@mixin h3-desk {
  @include title-default;
  font-size: px-to-rem(48);
}

@mixin h4-desk {
  @include title-default;
  font-size: px-to-rem(36);
}

@mixin h5-desk {
  @include title-default;
  font-size: px-to-rem(28);
}

@mixin h6-desk {
  @include title-default;
  font-size: px-to-rem(24);
}

// Title on Phone
// ...

/* Body Text */

@mixin body-text-default {
  color: var(--text-tale-title);
  font-family: var(--second-family);
  font-weight: 400;
  letter-spacing: -0.01em;
}

@mixin text-1-adaptive {
  @include body-text-default;
  font-size: px-to-rem(24) !important;
}

@mixin text-2-adaptive {
  @include body-text-default;
  font-size: px-to-rem(22) !important;
}

@mixin text-3-adaptive {
  @include body-text-default;
  font-size: px-to-rem(20) !important;
}

@mixin text-2b-desk {
  @include body-text-default;
  font-weight: 700;
  font-size: px-to-rem(22) !important;
}

@mixin text-4-adaptive {
  @include body-text-default;
  font-size: px-to-rem(18) !important;
}

@mixin text-4b-adaptive {
  @include body-text-default;
  font-weight: 700;
  font-size: px-to-rem(18) !important;
}

@mixin text-5-adaptive {
  @include body-text-default;
  font-size: px-to-rem(16) !important;
}

@mixin text-6-adaptive {
  @include body-text-default;
  font-size: px-to-rem(12) !important;
}

/* Other */
// ...

._text1 {
  @include text-1-adaptive;
}

._text2 {
  @include text-2-adaptive;
}

._text3 {
  @include text-3-adaptive;
}

._text4 {
  @include text-4-adaptive;
}

._text5 {
  @include text-5-adaptive;
}

._text6 {
  @include text-6-adaptive;
}

/* Typography classes */

._lowercase {
  text-transform: lowercase;
}

._uppercase {
  text-transform: uppercase;
}

._h1 {
  @include h1-desk;
}

._h2 {
  @include h2-desk;
}

._h3 {
  @include h3-desk;
}

._h4 {
  @include h4-desk;
}

._h5 {
  @include h5-desk;
}

._h6 {
  @include h6-desk;
}
